html, body, #root {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(121, 183, 202);
}


.card {
    background: black;
    border-radius: 1px;
    height: 250px;
    margin: 1.5rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
  }
  
  .card > .img-container {
    height: 80%;
    overflow: hidden;
    background:black;
  }
  .card > .img-container > img {
    width: 100%
  }
  
  .card > .content {
    font-size: 12px;
    color: grey;
  }
  
  .card > .content > ul {
    padding-left: 0;
    list-style-type: none;
  }

  
.wrapper {
  background-color: black;
  background-size: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  }
.instruct {
    width: 100%;
    margin: 15px 0px 0px 0px;
    text-align: center;
    font-size: 1.25rem;
    color:  rgb(121, 183, 202);
  }
ul.navList {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-size: 2rem;
  list-style-type:none
}

.logo, .score, .click {
  color: black;
}

.logo {
  font-weight: 900;
}


  
